.card__button {
  border: none;
  background: url(../../../images/like.svg) no-repeat;
  background-position: center;
  background-size: 22px 19px;
  margin: 0;
  padding: 0;
  width: 22px;
  height: 19px;
  transition: all 0.2s ease-in-out;
}

@media (hover: hover) {
  .card__button:hover {
    cursor: pointer;
    opacity: 0.5;
  }
}
