.popup__submit {
  margin: 0;
  border: none;
  padding: 14px 0;
  background: #000;
  border-radius: 2px;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  color: #fff;
  transition: opacity 0.2s ease-out;
}

.popup__submit:hover {
  cursor: pointer;
  opacity: 0.8;
}

@media only screen and (max-width: 767px) {
  .popup__submit {
    padding: 14.5px 0;
    font-size: 14px;
    line-height: 17px;
  }
}
