.popup__exit {
  border: none;
  margin: 0;
  padding: 0;
  position: absolute;
  left: 102%;
  top: -12%;
  background: url(../../../images/exit.svg);
  width: 32px;
  height: 32px;
  background-size: contain;
  transition: opacity 0.2s ease-in-out;
}

.popup__exit:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media only screen and (max-width: 767px) {
  .popup__exit {
    width: 20px;
    height: 20px;
    left: 95%;
    transform: translateX(-50%);
    top: -10%;
  }
}
