.profile__main {
  display: grid;
  margin: 0;
  padding: 0;
  grid-template-areas:
    'image title edit'
    'image descr .';
  row-gap: 16px;
}

@media only screen and (max-width: 767px) {
  .profile__main {
    row-gap: 0;
    grid-template-areas:
      'image image'
      'title edit'
      'descr descr';
  }
}
