.popup__input {
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  outline: none;
  padding: 5px 0;
  margin: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  width: 100%;
}

.popup__input:focus {
  outline: 2px solid lightblue;
}

.popup__input::placeholder {
  font-family: Inter;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  letter-spacing: 0em;
  text-align: left;
  color: #c4c4c4;
}
