.popup {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  position: absolute;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  visibility: hidden;
  opacity: 0;
  transition: all 0.35s ease-in-out;
  box-sizing: border-box;
}

@media only screen and (max-width: 767px) {
  .popup {
    padding: 0 19px;
  }
}
